import React,{useState} from "react";
import { useDispatch, useSelector } from 'react-redux'
import { Row,Col,Button, FormGroup, Input, Label,Card,CardBody,ListGroup, ListGroupItem
  } from "reactstrap"
import { crudActions } from '../../../../actions'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowDown, faSearch, faTimes } from "@fortawesome/free-solid-svg-icons";
import {toastr} from 'react-redux-toastr'

const SearchvArticulos = ({getComponent}) => {
    const dispatch = useDispatch()   
    const { data  } = useSelector(state => state.articulos)  
    const { items, item  } = useSelector(state => state.ventas)  
    const almacenId = JSON.parse(localStorage.getItem('@userAlmacen'))  
    const [open, setOpen] = useState(false); 
    const [name, setName] = useState('');
    const [cantidad, setCantidad] = useState(0);
    const [valor, setValor] = useState(0.00);
    const [articulo, setArticulo] = useState({});
    
    const changeHandler = event => {    
      const { value } = event.target  
      setName(value)      
    } 
    const search = () =>{    
      setOpen(true)  
      let iok ={      
      nombre    : name,
      almacenId : almacenId.id      
      }  
      dispatch(crudActions.GET_SEARCHS_ITEMS('ARTICULOS_DATA_ITEMS','tpv',iok))
    } 

    const clearInput = () => {
      setName('')
      setOpen(false)
      setCantidad(0)
      setValor(0)
    } 
    
    const upCantidad = (e) => {
      const { value } = e.target 

      if(articulo.stock >= parseInt(value) && parseInt(value) > 0)
      {
        setCantidad(value)
      }else{
        toastr.error('Stock', 'La cantidad es mayor al STOCK')
        setCantidad(0)  
      }      
    }

    const upValor = (e) => {
      const { value } = e.target 
      setValor(value)      
    } 

    const add = () =>{  
      let ites = [...items]
      let cTotal = item.nroItems     
      let gTotal = item.totalGeneral      
      let sTotal = item.subTotal
      let gImp   = item.impuesto
      let tDescuento = item.totalGeneral
      let repeat = false
      ites.map((item, index) =>{                        
        if(item.articuloId === articulo.articuloId)
        { 
          repeat = true;
        }
        return null
      }) 
      if(!repeat)
      {
      let itemVenta = {
        cantidad    : cantidad,          
        articuloId  : articulo.articuloId,
        codigo      : articulo.codigo, 
        marca       : articulo.marca,
        unidad      : articulo.unidad,
        valor       : parseFloat(valor),
        subTotal    : parseInt(cantidad) * parseFloat(valor),      
        nombre      : articulo.nombre,    
        ventaId     : item.id    
      }  
      ites.push(itemVenta);   

      cTotal = parseInt(cTotal) +parseInt(cantidad);    
      gTotal = parseFloat(gTotal) + parseFloat(itemVenta.subTotal)  
      gImp   = gTotal * parseFloat(item.iva / 100)
      sTotal = (gTotal - item.descuento) - parseFloat(gImp)    
      tDescuento = parseFloat(gTotal) + parseFloat(itemVenta.subTotal)  
      /*tDescuento = parseFloat(gTotal) - parseFloat(item.descuento)            */
      /*gTotal = parseFloat(gTotal) - parseFloat(item.descuento)*/
      dispatch({type:'VENTAS_SET_ITEMS',values:ites, cantidad: cTotal, subTotal: sTotal, totalGeneral: gTotal,impuesto:gImp, totalDescuento : tDescuento})  

      }
        clearInput()

    }

    const handleAsignar = (articulo) =>{   
      console.log(articulo)
      setArticulo(articulo)  
      setName(articulo.codigo+' - '+articulo.nombre)    
      setValor(articulo.precioVenta)    
      setOpen(false)
    }


    return (   
      <>           
        <Row form>                      
          <Col md={6}>
            <FormGroup>   
              <Label for="eNombre">Nombres</Label>                 
                <Input 
                  type="text" 
                  name="name"                             
                  id="name"  
                  value={name || ''}  
                  onChange={changeHandler} />
                   {
                              name ? 
                              <Button className="volatil" onClick={(e) => {clearInput()}}>
                                <FontAwesomeIcon icon={faTimes}   />
                              </Button>
                              : null
                            } 
            </FormGroup>
          </Col>
          <Col md={1}>
                        <FormGroup>   
                          <Button className="btn-primary btn-search mt-3"
                          onClick={search}>
                            <FontAwesomeIcon icon={faSearch} />
                          </Button>  
                        </FormGroup>
                      </Col>

                      <Col md={2}>
                        <FormGroup>   
                        <Label for="eNombre">Cantidad</Label>                 
                          <Input 
                            type="number" 
                            name="cantidad"                             
                            id="cantidad"    
                            value={cantidad}                          
                            onChange={(e) => upCantidad(e)} />
                        </FormGroup>
                      </Col>
                      <Col md={2}>
                        <FormGroup>   
                        <Label for="eValor">Valor</Label>                 
                          <Input 
                            type="number" 
                            name="valor"                             
                            id="valor"    
                            value={valor}      
                            readOnly={true}                    
                            onChange={(e) => upValor(e)} />
                        </FormGroup>
                      </Col>
                                      
                      <Col md={1}>
                        <Button className={cantidad === 0 ? "btn-rd btn-disabled mt-3":"btn-rd btn-danger mt-3"}
                          onClick={() => add() }
                        >
                         <FontAwesomeIcon icon={faArrowDown} />                          
                         </Button>
                      </Col>
                    </Row>                  
      
              
              { open ?              
                <Card className="resultArt">           
                  <CardBody>
                    {data &&
                    <ListGroup>
                      {data.map(item =>(
                        <ListGroupItem
                          key={item.id}
                          onClick={() => handleAsignar(item) }
                        >
                        <b>{item.codigo}</b> - {item.nombre}/{item.marca} - (Stock: {item.stock})  
                        </ListGroupItem>
                      ))}
                    </ListGroup>
                    }
                  </CardBody>  
                </Card>           
              : null}                

        </>                    
                         
    );
};
export default SearchvArticulos;
